@keyframes slideFromBottom {
  0% {
    opacity: 0%;
    transform: translateY(+200%);
  }
  100% {
    opacity: 100%;
    transform: translateY(0);
  }
}

.meal_page {
  animation: 1s slideFromBottom ease-out;
}

.mealpage_search_parameters_block, .buttons_block, .main_block {
  display: flex;

  font-family: Ubuntu;

  max-width: 900px;
  width: 100%;
  
  justify-content: space-between;
  gap: 16px;

  margin: 16px auto;
}

.mealpage_search_parameters_block {
  flex-direction: row;

  .mealpage_search_parameter_select {
    width: 500px;
  }
}

.buttons_block {
  flex-direction: row;

  button {
    font-family: Ubuntu;

    border-radius: 5px;
    border: 1px solid black;

    background-color: aliceblue;
    width: 350px;
    height: 50px;

    transition: all 0.15s;
    &:hover {
      letter-spacing: 0.5px;
      cursor: pointer;
      background-color: #4abaff;
    }
  }
}

.main_block {
  flex-direction: column;

  .status_text {
    font-style: italic;
  }
}

.information_block {
  background-color: rgb(240, 240, 240);
  padding: 32px;
  border-radius: 5px;
  border: none;

  .meal_title {
    font-size: 32px;
    margin: 0;
  }

  .meal_subtitle {
    font-size: 16px;
    font-style: italic;
    margin-top: 6px;
    margin-bottom: 30px;
  }

  .meal_yt_link {
    font-family: Ubuntu;
    display: block;
    color: red;
    text-decoration: underline;
    font-size: 15px;
  }
}

@media screen and (max-width: 900px) {
  .mealpage_search_parameters_block {
    width: 90%;
  }

  .buttons_block {
    width: 90%;
  }
}

@media screen and (max-width: 450px) {
  .mealpage_search_parameters_block {
    flex-direction: column;

    align-items: center;
    width: 100%;

    .mealpage_search_parameter_select {
      width: 90%;
    }
  }

  .buttons_block {
    flex-direction: column;
    align-items: center;
  }
}